.TaskArea {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  padding: 1em;
  background-color: #eeeeee;
  grid-gap: .5em; }
  .TaskArea h1 {
    text-align: center;
    display: grid;
    grid-template-columns: 1fr auto 1fr;
    grid-gap: 20px; }
    .TaskArea h1:before {
      content: "";
      -ms-flex-item-align: center;
          align-self: center;
      border-top: 1px solid #999; }
    .TaskArea h1:after {
      content: "";
      -ms-flex-item-align: center;
          align-self: center;
      border-top: 1px solid #999; }
  .TaskArea .header {
    grid-column: 1/5; }
  .TaskArea .header-column {
    font-size: 1.2em;
    font-weight: 400; }
  .TaskArea .models {
    grid-column: 2/5; }



